import InstagramEmbed from 'react-instagram-embed';

import React from 'react'

export default function insta({url}) {
  return (
    <div className="insta">
      <InstagramEmbed
        url={url}
        maxWidth="100%"
        hideCaption={true}
        containerTagName='div'
        protocol=''
        injectScript
        onLoading={() => {}}
        onSuccess={() => {}}
        onAfterRender={() => {}}
        onFailure={() => {}}
      />
    </div>
  )
}


